@import url(https://fonts.googleapis.com/css?family=Merriweather&display=swap);
/* Font import from Google Fonts */

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --defaultFont: "Merriweather";
}

* {
  box-sizing: border-box;
}

#root {
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-image: url(/portfolio/home/static/media/background.55f2a440.jpg);
  background-size: cover;
  background-position: center;
}

#Navbar-container {
  height: 5%;
  width: 85%;
  display: flex;
  justify-content: space-evenly;
  align-items: flex-end;
}

.Navbar-selection {
  padding: 0 0 5px 0;
}

.Navbar-selection a {
  text-decoration: none;
  color: rgb(255, 255, 255);
  font-family: var(--defaultFont);
  font-size: 20px;
  text-shadow: 1px 1px 0 black;
}

.Navbar-selection:hover {
  cursor: pointer;
  padding: 0;
  border-bottom: 5px solid rgb(160, 0, 0);
}

.selected {
  padding: 0;
  border-bottom: 5px solid rgb(160, 0, 0);
}

.PortItem-project {
  list-style-type: disc;
  font-size: 100%;
  margin: 5px;
}

.PortItem-project a {
  font-family: var(--defaultFont);
  text-decoration: underline;
  color: black;
}

.PortItem-project a:hover {
  color: maroon;
}

#Home-picContainer {
  width: 85%;
  padding-bottom: 15px;
  border-bottom: 2px solid black;
  display: flex;
  justify-content: center;
}

#Home-picture {
  height: 275px;
  width: 275px;
  background-image: url(/portfolio/home/static/media/me.c8854588.jpg);
  background-size: cover;
  background-position: center;
  border: 2px solid white;
  border-radius: 25px;
  box-shadow: 0 0 6px black;
}

#Home-aboutMe {
  width: 80%;
  text-align: center;
  font-family: var(--defaultFont);
}

.Resume-tabSpace {
  height: 100%;
  width: 100%;
}

.Resume-tabSpace ul {
  margin: 10px;
}

#Resume-container {
  width: 90%;
  height: 95%;
}

.sectionHeader {
  text-decoration: underline;
  margin: 0;
}

.buttonContainer {
  width: 100%;
  margin: 5px;
  display: flex;
  justify-content: center;
}

#reduxLogo {
  width: 100%;
  height: 100%;
  background-image: url(/portfolio/home/static/media/redux.cdb6953a.png);
  background-size: contain;
  background-position: center;
}

h4 {
  margin: 3px;
}

.Portfolio-header {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Portfolio-header h2 {
  margin: 5px;
  font-family: var(--defaultFont);
  color: maroon;
}

.Portfolio-header h3 {
  margin: 2px;
  text-decoration: underline;
  color: white;
  text-shadow: 1px 1px 1px black;
}

.Portfolio-header h4 {
  margin: 10px 10px 2px 10px;
  font-weight: bold;
  text-align: center;
}

.Portfolio-projects {
  width: 75%;
}

#Contact-phone {
  font-family: var(--defaultFont);
  font-size: 25px;
}

#Contact-social {
  width: 30%;
  border-radius: 10px;
  display: flex;
  justify-content: space-evenly;
}

#Contact-email {
  font-family: var(--defaultFont);
  font-size: 25px;
}

#Contact-email a {
  color: black;
}

#Contact-email a:hover {
  color: maroon;
}

.socialIcon {
  color: white;
}

.socialIcon:hover {
  color: maroon;
}

p {
  margin: 10px;
  padding: 0;
}

#NotFound-container {
  text-align: center;
}

#App-container {
  width: 85%;
  height: 100%;
  margin: 0 0 5px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: rgba(255, 255, 255, 0.664);
  border-radius: 25px;
  overflow: scroll;
}

#App-container::-webkit-scrollbar {
  display: none;
}

h1 {
  margin: 10px 0 5px 0;
  padding: 0;
  font-size: 35px;
  font-family: var(--defaultFont);
  text-decoration: underline;
  text-shadow: 1px 1px 0 black;
  color: rgb(255, 255, 255);
}

